.wrapper{
  display: flex;
  justify-content: center;
  
  width: 100%;
  background: #222;
}


input{
  height: 30px;
  width: 250px;
  border:none;
  border-bottom:  1px solid whitesmoke;
  background: none;
  color: #fff;
  padding: 10px;
  outline:none;
  text-decoration: none;
  appearance: none;
  
}
input:focus{font-size:16px;}

input::-webkit-inner-spin-button
{
-moz-appearance: textfield;
}

input::-webkit-inner-spin-button {
  display: none;
  }

.enter{
  width: 150px;
  height: 30px;
  font-size: 12px;
  background: none;
  border: 1px solid whitesmoke;
  color: whitesmoke;
  margin-left: 10px;
}

enter:hover{
  background: whitesmoke;
  color: #222222;
  cursor: pointer;
}

.todo__item{
  position: absolute;
  cursor:move;
  min-width: 215px;
  color: black;
  padding: 1em;
  border-radius: 5px;
  width: 50px;
}

.delete{
  position: absolute;
  right: 5px;
  top: 5px;
  background: none;
  border: none;
  font-size: 20px
}

.delete:hover{
  color: red;
  cursor: pointer;
}

.topnav{
  background-color: #222;
  min-height: 100vh;
}

.logotext{
  color: whitesmoke;
  font-size: 30px;
  margin-left: 20px;

}



.logo{
  width: 200px;
  padding-left: 50px;
  padding-top: 10px;
}

@media (max-width:500px) {
  input{
    height: 20px;
    width: 150px;
    border:none;
    border-bottom:  1px solid whitesmoke;
    background: none;
    color: #fff;
    padding: 10px;
    outline:none;
  }
  .wrapper{
    display: flex;
    justify-content: top;
    align-items:flex-start;
    padding-top: 100px;
    min-height: 100vh;
    width: 100%;
    background: #222;
  }


  .enter{
    width: 150px;
    height: 50px;
    font-size: 12px;
    background: none;
    border: 1px solid whitesmoke;
    color: whitesmoke;
    margin-left: 10px;
  }
  
  enter:hover{
    background: whitesmoke;
    color: #222222;
    cursor: pointer;
  }
  
  .todo__item{
    position: absolute;
    cursor:move;
    width: 50%;
    height: auto;
    color: black;
    padding: 1em;
    border-radius: 5px;
  }

  
  .delete{
    position: absolute;
    right: 5px;
    top: 5px;
    background: none;
    border: none;
    font-size: 20px
  }
  
  .delete:hover{
    color: red;
    cursor: pointer;
  }

  .logo{
    width: 200px;
    display: block;
    padding-left: 25%;
    padding-top: 10px;
  }

}

#debug{
  display: none;
}